"use client"

import { useState, type ReactNode } from "react"
import { Copy, Check } from "lucide-react"
import { urlFor } from "@/lib/sanityImage"

interface PTChildrenProps {
  children: ReactNode
}

interface PTValueProps {
  value: any
}

export const portableTextComponents = {
  /* ----------------------------------------
     BLOCK TYPES
  ---------------------------------------- */
  types: {
    image: ({ value }: PTValueProps) => {
      if (!value?.asset) return null

      return (
        <div className="my-10">
          <img
            src={urlFor(value).width(900).url()}
            alt={value.alt || "Blog image"}
            className="w-full rounded-2xl"
          />
          {value.caption && (
            <p className="text-center text-sm text-gray-500 mt-2">
              {value.caption}
            </p>
          )}
        </div>
      )
    },

    code: ({ value }: PTValueProps) => {
      const [copied, setCopied] = useState(false)

      return (
        <div className="my-10 bg-gray-900 rounded-2xl overflow-hidden">
          {/* Header */}
          <div className="flex items-center justify-between px-4 py-2 bg-gray-800 text-sm text-gray-300">
            <span>{value.filename || "Code"}</span>
            <button
              onClick={() => {
                navigator.clipboard.writeText(value.code)
                setCopied(true)
                setTimeout(() => setCopied(false), 2000)
              }}
              className="flex items-center gap-1 text-xs hover:text-white"
            >
              {copied ? <Check size={14} /> : <Copy size={14} />}
              {copied ? "Copied" : "Copy"}
            </button>
          </div>

          {/* Code */}
          <pre className="p-5 overflow-x-auto text-sm text-gray-100 leading-relaxed">
            <code>{value.code}</code>
          </pre>
        </div>
      )
    },
  },

  /* ----------------------------------------
     MARKS (INLINE)
  ---------------------------------------- */
  marks: {
    link: ({ value, children }: PTValueProps & PTChildrenProps) => (
      <a
        href={value?.href}
        target={value?.blank ? "_blank" : undefined}
        rel={value?.blank ? "noopener noreferrer" : undefined}
        className="text-blue-600 underline hover:text-blue-800"
      >
        {children}
      </a>
    ),
  },

  /* ----------------------------------------
     LISTS
  ---------------------------------------- */
  list: {
    bullet: ({ children }: PTChildrenProps) => (
      <ul className="list-disc pl-6 my-6 space-y-2">{children}</ul>
    ),
    number: ({ children }: PTChildrenProps) => (
      <ol className="list-decimal pl-6 my-6 space-y-2">{children}</ol>
    ),
  },

  listItem: {
    bullet: ({ children }: PTChildrenProps) => <li>{children}</li>,
    number: ({ children }: PTChildrenProps) => <li>{children}</li>,
  },
}